$white: #fff;
$blue-accent: #00acdc;
$main-accent: #060b35;
$secondary-color: #616070;

$line-height: 14px;
$line-height-lg: 18px;

$font-size-md: 12px;

$card-count-row: 4;
$card-width: 200px;
$card-indent-right: 48px;
$card-indent-bottom: 46px;

$content-max-width: $card-count-row * $card-width
  + ($card-count-row - 1) * $card-indent-right;

$stars-count: 5;
$stars-size: 16px;

$logo-size: 40px;
